@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Lexend:wght@100;300;400;700&display=swap);
.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  height: 70px;
  margin: 0 4%;
}

.NavMain .nav-list {
  margin-left: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.Dashboard__navbar {
  width: 15%;
  background: var(--color-main);
  color: white;
  height: 80vh;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.Dashboard__navbar-link {
  margin: 40px 0;
  font-size: 1.3em;
}

.Dashboard__container {
  position: absolute;
  left: 15%;
  top: 60px;
}

.Dashboard__icons {
  color: var(--color-fourth);
}

p {
    color: var(--color-text);
    line-height: 1.7rem;
}

.chart-grid {
    display: grid;
    grid-template-columns: 1.3fr 0.7fr;
    grid-template-rows: 1fr;
    grid-gap: 10px 100px;
    gap: 10px 100px;
    grid-template-areas:
      ". .";
    margin: 5rem 0;
  }

.chart {
    min-width: 0;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.method {
    font-size: 0.9rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.cartes-annees a {
    padding-right: 20px;
}

.cartes-annees li, .padding-top {
    margin-top: 15px;
}
img {
    height: 500px;
    width: auto;
    display: -webkit-flex;
    display: flex;
}

.gallery-container {
    -webkit-align-items: center;
            align-items: center;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Lexend", sans-serif;
}

:root {
  --color-main: hsla(275, 89%, 26%, 0.918);
  --color-second: #cf173ce5;
  --color-third: #e6e6fae0;
  --color-fourth: #f17d52e1;
  --color-text: #646464;
  --color-input: #f9f5f5;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: grey;
}

ul {
  list-style-type: none;
}

.title-1 {
  border-bottom: 3px solid hsla(275, 89%, 26%, 0.918);
  border-bottom: 3px solid var(--color-main);
  padding: 15px 15px 3px;
  display: inline-block;
}

.title-2 {
  border-bottom: 3px solid #cf173ce5;
  border-bottom: 3px solid var(--color-second);
  padding: 15px 15px 3px;
  display: inline-block;
}

.title-3 {
  border-bottom: 3px solid #f17d52e1;
  border-bottom: 3px solid var(--color-fourth);
  padding: 15px 15px 3px;
  display: inline-block;
}

.color-1 {
  background-color: hsla(275, 89%, 26%, 0.918);
  background-color: var(--color-main);
}

.color-2 {
  background-color: #cf173ce5;
  background-color: var(--color-second);
}

.color-3 {
  background-color: #e6e6fae0;
  background-color: var(--color-third);
}

.color-4 {
  background-color: #f17d52e1;
  background-color: var(--color-fourth);
}

.btn-1 {
  text-decoration: none;
  color: white;
  font-size: larger;
  background-color: hsla(275, 89%, 26%, 0.918);
  background-color: var(--color-main);
  padding: 15px;
  padding-left: 35px;
  padding-right: 35px;
  margin: 10px;
  border: none;
  border-radius: 15px;
}

.btn-1:hover {
  box-shadow: 5px 7px #e6e6fae0;
  box-shadow: 5px 7px var(--color-third);
}

.btn-2 {
  text-decoration: none;
  border: none;
  color: white;
  font-size: larger;
  background-color: #cf173ce5;
  background-color: var(--color-second);
  padding: 15px;
  padding-left: 35px;
  padding-right: 35px;
  margin: 10px;
  border-radius: 15px;
}

.btn-2:hover {
  box-shadow: 5px 7px #e6e6fae0;
  box-shadow: 5px 7px var(--color-third);
}

.btn-3 {
  text-decoration: none;
  border: none;
  color: white;
  font-size: larger;
  background-color: #f17d52e1;
  background-color: var(--color-fourth);
  padding: 15px;
  padding-left: 35px;
  padding-right: 35px;
  margin: 10px;
  border-radius: 15px;
}

.btn-3:hover {
  box-shadow: 5px 7px #e6e6fae0;
  box-shadow: 5px 7px var(--color-third);
}

.form-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-decoration: none;
}

.form-label {
  font-size: larger;
  padding: 15px;
  margin: 20px;
}

.form-input {
  border: none;
  background-color: #f9f5f5;
  background-color: var(--color-input);
  padding: 15px;
  margin: 20px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-row {
  -webkit-flex-direction: row;
          flex-direction: row;
}

.page-container {
  margin: 4% 10% 12%;
}

*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
